
* {
  margin: 0;
  padding: 0;
}
a {
  color: #000;
  text-decoration: none;
}
a:active {
    color: #000;
}
html,
body,
#app {
  height: 100%;
  background-color: #f5f6f7;
}
#app {
  position: relative;
}
#app #header {
    height: 80px;
    display: block;
    background: #e8e8e8;
    background: #ececec;
    background: linear-gradient(0deg, #ececec 0%, #d2d2d2 94%, #d2d2d2 100%);
    color: #333333;
    line-height: normal;
    font-size: 16px;
    position: sticky;
    z-index: 999;
    width: 100%;
    box-shadow: 0 -3px 8px 2px rgba(0, 0, 0, 0.6);
    border-bottom: solid 1px #4e4944;
}
#app #header .header-block {
      max-width: 1380px;
      margin: 0 auto;
      height: 100%;
}
#app #header .header-block .vajralogo {
        float: left;
        height: 29px;
        padding-top: 26px;
        transition: all 0.4s ease-out;
}
#app #header .header-block .vajralogo:hover {
          filter: grayscale(1);
}
#app #header .header-block .npm,
      #app #header .header-block .github {
        float: right;
        text-align: right;
        margin-top: 22px;
        width: 30px;
        height: 22px;
}
#app #header .header-block .npm .npmlogo,
        #app #header .header-block .npm .ghlogo,
        #app #header .header-block .github .npmlogo,
        #app #header .header-block .github .ghlogo {
          height: 22px;
          margin-left: 10px;
          transition: all 0.4s ease-out;
          filter: grayscale(1);
}
#app #header .header-block .npm .ghlogo,
        #app #header .header-block .github .ghlogo {
          opacity: 0.1;
}
#app #header .header-block .npm .ghlogo:hover,
          #app #header .header-block .github .ghlogo:hover {
            opacity: 1;
}
#app #header .header-block .npm .npmlogo,
        #app #header .header-block .github .npmlogo {
          opacity: 0.2;
}
#app #header .header-block .npm .npmlogo:hover,
          #app #header .header-block .github .npmlogo:hover {
            filter: grayscale(0);
            opacity: 1;
}
#app #header .header-block img.npm-code {
        position: relative;
        right: -80px;
        top: 59px;
        float: right;
        z-index: 99;
}
#app #content {
    position: absolute;
    top: 80px;
    bottom: 0;
    width: 100%;
}
#app #content .img-info {
      font-family: Arial, Helvetica, sans-serif;
      position: absolute;
      top: 0;
      left: 0;
      width: 97.8%;
      height: 100%;
      text-align: center;
      opacity: 0;
      transition: all 0.2s ease-out;
      transition-delay: 0.05s;
}
#app #content .img-info .title-info,
      #app #content .img-info .desc-info {
        padding: 10px 4px;
        position: absolute;
        transition: all 0.3s ease-out;
        transition-delay: 0.1s;
        background-color: rgba(255, 255, 255, 0.8);
        width: calc(100% - 20px);
        margin-left: 10px;
}
#app #content .img-info .title-info {
        top: -10px;
        color: #676464;
        text-rendering: geometricPrecision;
        display: block;
        box-shadow: 0px 4px 4px -4px rgba(0, 0, 3, 0.7);
}
#app #content .img-info .desc-info {
        bottom: -15px;
        font-size: 14px;
        color: #5f5f5f;
        display: block;
        box-shadow: 0px -4px 6px -5px rgba(0, 0, 3, 0.75);
}
#app #content .img-info:hover {
        opacity: 1;
}
#app #content .img-info:hover .title-info {
          top: 10px;
}
#app #content .img-info:hover .desc-info {
          bottom: 10px;
}
#app {
  overflow: auto;
  position: relative;
}
#app .some-info {
    line-height: 1.6;
    text-align: center;
}
@media only screen and (max-width: 1399px) {
body #app #header .header-block {
    max-width: 1040px;
}
}
@media only screen and (max-width: 1049px) {
body #app #header .header-block {
    max-width: 680px;
}
}
@media only screen and (max-width: 699px) {
body #app #header .header-block {
    max-width: 100%;
    margin: 0 30px;
}
}
@media only screen and (max-width: 444px) {
body #app #header .header-block img.npm-code {
    position: absolute;
    right: 15px;
}
}

